.data-1{
    position: relative;
    width: 900px;
    margin: 20px auto;
    background-color: white;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
    padding: 10px;  
}
.data-title{
    border-bottom: 1px solid grey;
    text-align: center;
    padding-bottom: 10px;
    font-size: 2rem;
}
.data-title span{
    font-size: 1rem;
}
.queData{
    margin: 10px 0;
}
.queData div>*{
    margin-bottom: 5px;
}
.queData>div{
    margin: 10px;
    padding: 10px;
    border: 1px solid grey;
}
.queData>div p:first-of-type{
    font-size: 1.2rem;
}
.queData>div p:last-of-type{
    font-size: 0.8rem;
}
.data-foot{
    margin-top: 10px;
    text-align: center;
}
.data-foot input[type=button]{
    border: 1px solid black;
    transition: all .5s;
    background-color: white;
    padding: 10px 100px;
    outline: none;
}
.data-foot input[type=button]:hover{
    background-color: rgba(255, 0, 0, 0.5);
}